@import url("https://fonts.googleapis.com/css2?family=Madimi+One&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Madimi+One&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-color: #dec9e9;
  --secondary-color: #dac3e8;
  --tertiary-color: #d2b7e5;
  --fourth-color: #c19ee0;
  --fifth-color: #b185db;
  --sixth-color: #a06cd5;
  --seventh-color: #9163cb;
  --eighth-color: #815ac0;
  --ninth-color: #7251b5;
  --tenth-color: #6247aa;
}

html {
  scrollbar-color: var(--fifth-color) var(--primary-color);
  scrollbar-width: thin;
  scrollbar-track-color: var(--primary-color);
  scroll-padding-inline: 1px 10px;
  overflow-x: hidden;
}

body {
  background-color: #030014;
  color: white;
  scroll-behavior: smooth;
  overflow-x: hidden;
  width: 100%;
}

.custom-text-color {
  @apply bg-gradient-to-r from-[#9d59ef] to-[#dd524b] bg-clip-text text-transparent;
  /* color: #a8dadc; */
}

.shadow-custom-color {
  box-shadow: 1px 4px 4px 1px var(--sixth-color);
}

circle,
rect,
line {
  stroke-width: 10px;
  stroke-linecap: round;
  fill: transparent;
}

.active {
  color: var(--seventh-color);
}

.image-animate {
  animation: image-animate 5s linear infinite alternate;
}

@keyframes image-animate {
  0% {
    transform: translateY(0) rotate(0deg) scale(0.75);
  }

  25% {
    transform: translateY(-25px) rotate(7deg) scale(0.8);
  }

  50% {
    transform: translateY(-40px) rotate(15deg) scale(0.9);
  }

  100% {
    transform: translateY(0) rotate(0deg) scale(1);
  }
}

.canvas-loader {
  font-size: 10px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  /* position: relative; */
  text-indent: -9999em;
  animation: mulShdSpin 1.1s infinite ease;
}

@keyframes mulShdSpin {
  0%,
  100% {
    box-shadow: 0em -2.6em 0em 0em #ffffff,
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.2),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.5),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7);
  }
  12.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.7),
      1.8em -1.8em 0 0em #ffffff, 2.5em 0em 0 0em rgba(255, 255, 255, 0.2),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5);
  }
  25% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.5),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7), 2.5em 0em 0 0em #ffffff,
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  37.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.7), 1.75em 1.75em 0 0em #ffffff,
      0em 2.5em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  50% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.5),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.7), 0em 2.5em 0 0em #ffffff,
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  62.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.2),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.5),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.7), -1.8em 1.8em 0 0em #ffffff,
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  75% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.2),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.5),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.7), -2.6em 0em 0 0em #ffffff,
      -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  87.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2),
      1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2),
      2.5em 0em 0 0em rgba(255, 255, 255, 0.2),
      1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2),
      0em 2.5em 0 0em rgba(255, 255, 255, 0.2),
      -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.5),
      -2.6em 0em 0 0em rgba(255, 255, 255, 0.7), -1.8em -1.8em 0 0em #ffffff;
  }
}

.heading-animate {
  animation: heading-animate 5s linear infinite alternate;
}

@keyframes heading-animate {
  0% {
    transform: translateY(0) scale(0.75);
  }

  25% {
    transform: translateY(-25px) scale(0.8);
  }

  50% {
    transform: translateY(-40px) scale(0.9);
  }

  100% {
    transform: translateY(0) scale(1);
  }
}

.shimmer-thumbnail {
  width: 120px;
  height: 90px;
  background: linear-gradient(-45deg, #eee 25%, #ddd 50%, #eee 75%);
  background-size: 200% 200%;
  animation: shimmer 1.5s infinite linear;
  border-radius: 4px;
}

.shimmer-details {
  flex-grow: 1;
  padding-left: 16px;
}

.shimmer-title {
  width: 80%;
  height: 16px;
  background: linear-gradient(-45deg, #eee 25%, #ddd 50%, #eee 75%);
  background-size: 200% 200%;
  animation: shimmer 1.5s infinite linear;
  border-radius: 4px;
  margin-bottom: 8px;
}

.shimmer-description {
  width: 100%;
  height: 12px;
  background: linear-gradient(-45deg, #eee 25%, #ddd 50%, #eee 75%);
  background-size: 200% 200%;
  animation: shimmer 1.5s infinite linear;
  border-radius: 4px;
}

@keyframes shimmer {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 200% 200%;
  }
}
